
import React, {Component} from 'react';
export default class About extends Component{
    render() {
      return (
  
        <section id="about">
          <div className="row">
            <div className="three columns">
              <img className="profile-pic" src="images/profilepic.jpg" alt />
            </div>
            <div className="nine columns main-col">
              <h2>About Me</h2>
              <p className="intro">A last semester student on his way to complete his Bachelor of Sciences degree in Software Engineering from one of the 
                renowned IT institutes in Pakistan, Punjab University College of Information Technology. I studied many courses in 
                my journey of Software Engineering which include OOP, Data Structures, Data Science and Algorithms.
                But a course I remember the most was not an IT specific, rather it was something that analyzed life
                and personalities, its traits, changing attitudes, life cycle of a human being and its development stages.
                So with a label of Software Enginnering, deep inside there lives a lover of Literature[poetry the most], nature and ...

              </p>
              <div className="row">
                <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
                    <span>Goal Chakkar</span><br />
                    <span>House No. 222<br />
                     District Nankana Sahib, Punjab, Pakistan.
                    </span><br />
                    <span>03462870231</span><br />
                    <span></span>
                  </p>
                </div>
                <div className="columns download">
                  <p>
                    <a href="#" className="button"><i className="fa fa-download" />View Resume</a>
                  </p>
                </div>
              </div> 
            </div> 
          </div>
        </section>
      );
    }
  };