import React, {Component} from 'react';
export default class Footer extends Component{
    render() {
          return (
      
        
            <footer>
              <div className="row">
                <div className="twelve columns">
                  <ul className="social-links">
                  <li><a target="blank" href="{}"><i className="fa fa-facebook" /></a></li>
                <li><a href="{this.props.resumeData.Twitter}"><i className="fa fa-twitter" /></a></li>
                {/* <li><a href="#"><i className="fa fa-google-plus" /></a></li> */}
                <li><a href="{this.props.resumeData.linkedin}"><i className="fa fa-linkedin" /></a></li>
                  </ul>
                  <ul className="copyright">
                    <li>© Copyright 2014 CeeVee</li>
                    <li>Made with love <a title="Styleshout" href="http://www.styleshout.com/">Styleshout</a></li>   
                  </ul>
                </div>
                <div id="go-top"><a className="smoothscroll" title="Back to Top" href="#home"><i className="icon-up-open" /></a></div>
              </div>
             </footer> 
             
          )
        }
      };
